import apex from '../apex';

const b64toBlob = (b64Data, contentType = '', sliceSize = 512) => {
  const byteCharacters = atob(b64Data);
  const byteArrays = [];

  for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
    const slice = byteCharacters.slice(offset, offset + sliceSize);
    const byteNumbers = new Array(slice.length);

    for (let i = 0; i < slice.length; i++) {
      byteNumbers[i] = slice.charCodeAt(i);
    }

    const byteArray = new Uint8Array(byteNumbers);
    byteArrays.push(byteArray);
  }

  const blob = new Blob(byteArrays, { type: contentType });
  return blob;
};

export const createDocumentFile = (fileInfo, name) => {
  const calls = fileInfo.numSlices;
  const blobsArray = [];
  let sliceNum = 0;
  let remainingSlices = fileInfo.numSlices;

  function saveFile() {
    const a = document.createElement('a');

    document.body.appendChild(a);
    a.setAttribute('style', 'display: none');
    return (data, fileName) => {
      const blob = new Blob(data, { type: 'text/csv' });
      const url = window.URL.createObjectURL(blob);

      if (
        (window.navigator.appVersion.toString().indexOf('.NET') > 0 ||
          window.navigator.appVersion.toString().indexOf('Edge') > 0) &&
        window.navigator.msSaveBlob
      ) {
        return window.navigator.msSaveBlob(blob, `${fileName}.csv`);
      }

      a.href = url;
      a.download = `${fileName}.csv`;
      a.click();
      return window.URL.revokeObjectURL(url);
    };
  }

  function createAndSaveFile(data) {
    if (data.statusCode === 'Success') {
      blobsArray.push(b64toBlob(data.base64Bytes));
      remainingSlices--;
      if (!remainingSlices) {
        const save = saveFile();
        save(blobsArray, name);
      }
    }
  }

  for (let i = 0; i < calls; i++) {
    apex.connection
      .DownloadDocumentSlice({
        descriptorId: fileInfo.descriptorId,
        sliceNum: sliceNum++
      })
      .then(res => {
        createAndSaveFile(res);
      });
  }
};

export const downloadDocument = (descriptorId, filename = 'report') => {
  apex.connection
    .DownloadDocument({
      descriptorId: descriptorId
    })
    .then(res => {
      createDocumentFile(res, filename);
    });
};



