import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../common/APButton';
import { getBEMClasses } from '../../helpers/cssClassesHelper';

import './ActivityReportingComponent.css';

const activityReportingClasses = getBEMClasses(['activity-reporting']);

const REPORT_TABS = {
  SINGLE: 0,
  CYCLICAL: 1
};

const ActivityReportingComponent = ({ openReportsSidePane }, context) => (
  <div className={activityReportingClasses()}>
    <div className={activityReportingClasses('item')}>
      <div className={activityReportingClasses('header-text')}>
        {context.t('Activity Reporting')}
      </div>
      <div className={activityReportingClasses('text')}>
        {context.t(
          'All of your activity on the exchange is available for you to download as a spreadsheet. Simply choose the type of activity and the timeframe to generate your report.'
        )}
      </div>
    </div>

    <div className={activityReportingClasses('item', 'create-reports')}>
      <div className={activityReportingClasses('create-new-header')}>
        {context.t('Create New:')}
      </div>

      <div className={activityReportingClasses('actions-holder')}>
        <APButton
          type="submit"
          onClick={() => openReportsSidePane({ index: REPORT_TABS.SINGLE })}
          customClass={activityReportingClasses()}>
          {context.t('Single Report')}
        </APButton>

        <APButton
          type="submit"
          onClick={() => openReportsSidePane({ index: REPORT_TABS.CYCLICAL })}
          customClass={activityReportingClasses()}>
          {context.t('Cyclical Report')}
        </APButton>
      </div>
    </div>
  </div>
);

ActivityReportingComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default ActivityReportingComponent;



