import { createSelector } from 'reselect';

export const scheduledReportsSelector = createSelector(
  state => state.report.reports,
  reports => {
    return reports.filter(report => report.ReportFrequency !== 'onDemand');
  }
);

export const cancelableScheduledReportsSelector = createSelector(
  scheduledReportsSelector,
  reports => {
    return reports.filter(
      report =>
        report.RequestStatus !== 'UserCancelled' &&
        report.RequestStatus !== 'SysRetired'
    );
  }
);



