import ActivityReportingComponent from './ActivityReportingComponent';
import { openReportsSidePane } from '../../redux/actions/sidePaneActions';

const mapDispatchToProps = {
  openReportsSidePane
};

export default connect(
  null,
  mapDispatchToProps
)(ActivityReportingComponent);



